import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Avatar, Button, Card, Col, Row} from "antd";
import Flex from "../../../../components/shared-components/Flex";
import {
    GlobalOutlined,
    MailOutlined,
    HomeOutlined,
    PhoneOutlined
} from '@ant-design/icons';
import { Icon } from '../../../../components/util-components/Icon'
import AvatarCustom from "../../../../components/shared-components/avatar-custom";

const CompTravelProfile = props=> {
    let {loading,data,message,error,type} = props
    let thumbnail = Array.isArray(data?.photo) && data?.photo.length > 0 ? [data?.photo[0]?.prefix,data?.photo[0]?.url].join("") : null
    return (
        <Card bordered={false} loading={loading ?? false}>
            <Row justify="center">
                <Col sm={24} md={23}>
                    <div className="d-md-flex">
                        <div className="mx-auto" style={{'marginTop': '-3.5rem', 'maxWidth': `${150+ 16}px`}}>
                            <AvatarCustom
                                size={150}
                                groups={data?.photos ?? data?.photo ?? []} thumbnail={thumbnail}/>
                        </div>
                        <div className="ml-md-4 w-100">
                            <Flex alignItems="center" mobileFlex={false} justifyContent={'between'} className="mb-3 text-md-left text-center">
                                <h2 className="mb-0 mt-md-0 mt-2">{data?.name ?? "-"}</h2>
                                <div className="ml-md-3 mt-3 mt-md-0">
                                    <Button size="small" type="primary">Get Ticket</Button>
                                </div>
                            </Flex>
                            <Row gutter="16">
                                <Col sm={24} md={8}>
                                    <p className="mt-0 mr-3 text-muted text-md-left text-center">
                                        It is a long established fact that a reader will be distracted.
                                    </p>
                                </Col>
                                <Col xs={24} sm={24} md={8}>
                                    <Row className="mb-2">
                                        <Col xs={12} sm={12} md={9}>
                                            <Icon type={MailOutlined} className="text-primary font-size-md"/>
                                            <span className="text-muted ml-2">Email:</span>
                                        </Col>
                                        <Col xs={12} sm={12} md={15}>
                                            <span className="font-weight-semibold">ellarbae@coolmail.io</span>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col xs={12} sm={12} md={9}>
                                            <Icon type={PhoneOutlined} className="text-primary font-size-md"/>
                                            <span className="text-muted ml-2">Phone:</span>
                                        </Col>
                                        <Col xs={12} sm={12} md={15}>
                                            <span className="font-weight-semibold">+12 123 1234</span>
                                        </Col>
                                    </Row>
                                </Col>
                                <Col xs={24} sm={24} md={8}>
                                    <Row className="mb-2 mt-2 mt-md-0 ">
                                        <Col xs={12} sm={12} md={9}>
                                            <Icon type={HomeOutlined} className="text-primary font-size-md"/>
                                            <span className="text-muted ml-2">Address:</span>
                                        </Col>
                                        <Col xs={12} sm={12} md={15}>
                                            <span className="font-weight-semibold">Los Angeles, CA</span>
                                        </Col>
                                    </Row>
                                    <Row className="mb-2">
                                        <Col xs={12} sm={12} md={9}>
                                            <Icon type={GlobalOutlined} className="text-primary font-size-md"/>
                                            <span className="text-muted ml-2">Website:</span>
                                        </Col>
                                        <Col xs={12} sm={12} md={15}>
                                            <span className="font-weight-semibold">ellarbae.io</span>
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>
                        </div>
                    </div>
                </Col>
            </Row>
        </Card>
    )
}

CompTravelProfile.propTypes = {}
CompTravelProfile.defaultProps = {}

export default connect(({Travel})=> {
    let { loading ,data, message ,error, type} = Travel
    return {loading,data,message,error,type}
},{})(React.memo(CompTravelProfile))
