import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import LayoutContent from "../../../../components/layout-components/LayoutContent";
import {PageHeaderAlt} from "../../../../components/layout-components/PageHeaderAlt";
import {Avatar, Button, Card, Col, Row, Tabs, Typography} from "antd";
import Container from "../../../../components/layout-components/container";
import {_getTravelSingle} from "../../../../redux/actions";
import {useParams} from "react-router-dom";
import TravelNearby from "../../../../components/layout-components/travel/travel-nearby";
import CompTravelProfile from "./comp-travel-profile";
import TabsPhotos from "./tabs-photos";
import TabsVideo from "./tabs-video";
import CompDiscussions from "./comp-discussions";
const TravelSingle = (props)=> {
    let { _getTravelSingle ,data} = props
    let { id } = useParams()
    useEffect(()=> {
        _getTravelSingle(id, {
            orderBy:"slug",
            type:"get"
        })
    },[_getTravelSingle])
    return (
        <LayoutContent style={{marginTop: 120, minHeight: "calc(100vh)"}}>
            <React.Fragment>
                <PageHeaderAlt background="/img/others/bg-doodle.jpg" cssClass="bg-primary" overlap>
                    <div className="py-5 my-md-5 text-center">
                        <div>
                            <Typography.Title level={1} className={'text-white font-weight-bold'}>{data?.name ?? data?.travelName ?? "-"}</Typography.Title>
                            <Typography.Text className={'text-white'}>{data?.slug ?? "-"}</Typography.Text>
                        </div>
                    </div>
                </PageHeaderAlt>
                <Container size={'md'}>
                    <>
                        <CompTravelProfile
                            loading={props?.loading ?? false}
                        />

                        <Row gutter={16}>
                            {/*<Col*/}
                            {/*    xs={{*/}
                            {/*        span:24,*/}
                            {/*        order:2,*/}
                            {/*    }}*/}
                            {/*    lg={{*/}
                            {/*        span:8,*/}
                            {/*        order:1*/}
                            {/*    }}*/}
                            {/*>*/}
                            {/*    <Card>*/}
                            {/*    </Card>*/}
                            {/*</Col>*/}
                            <Col
                                xs={{
                                    span:24,
                                    order:1,
                                }}
                                lg={{
                                    span:24,
                                    order:2
                                }}
                            >

                                {/**DISCUSSIONS**/}
                                <CompDiscussions/>


                                <Card>
                                    <Tabs defaultActiveKey="1">
                                        <Tabs.TabPane tab={'Photos'} key={'1'}>
                                            <TabsPhotos/>
                                        </Tabs.TabPane>
                                        <Tabs.TabPane tab={'video'} key={'2'}>
                                            <TabsVideo/>
                                        </Tabs.TabPane>
                                    </Tabs>
                                </Card>


                                <Card
                                    title={'Travel Recomendation'}
                                    loading={props?.loading ?? false}
                                    bordered={false}
                                >
                                    <TravelNearby
                                        {...props}
                                        notIn={[data?._id]}
                                        position={'left'}
                                        className={''}
                                        style={{}}
                                    />
                                </Card>

                            </Col>

                        </Row>
                    </>
                </Container>
            </React.Fragment>
        </LayoutContent>
    )
}

TravelSingle.propTypes = {
    _getTravelSingle: PropTypes.func.isRequired
}
TravelSingle.defaultProps = {
    _getTravelSingle: ()=> {

    }
}

export default connect(({Travel})=> {
    let { loading ,data, message ,error, type} = Travel
    return {loading,data,message,error,type}
},{_getTravelSingle})(TravelSingle)
