import React from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import AvatarCustom from "../../../../components/shared-components/avatar-custom";
import {Row, Col, Image} from 'antd'
import Flex from "../../../../components/shared-components/Flex";
import {LoadingOutlined} from "@ant-design/icons";
import Utils from "../../../../utils";


const TabsPhotos = (props) => {

    let {data, loading, message} = props

    return !loading && (
        <div className="w-full py-4">
            <div className="d-md-flex">
                <div className="!grid !grid-cols-3 gap-4 !w-full">
                    {
                        typeof (data?.photo) !== "undefined" &&
                        Array.isArray(data?.photo) &&
                        data?.photo.length > 0 &&
                        data?.photo.map((item, index) => (
                            <div className="w-full col-span-1 h-[180px] rounded-xl overflow-hidden  relative bg-red-500" key={`avatar-custom-${index}`}>
                                <Image
                                    style={{
                                        minWidth:"100%",
                                        width:"100%",
                                        minHeight:"180px",
                                        objectFit:"cover"
                                    }}
                                    fallback={'/img/image-notfound.png'}
                                    src={Utils.ImagePath(item?.prefix, item?.url)}
                                />
                                {/*<img*/}
                                {/*    className={'w-full h-full object-cover'}/>*/}

                            </div>
                        ))
                    }
                </div>
                <Row gutter={24}>


                </Row>
            </div>
        </div>
    )
}
TabsPhotos.propTypes = {}
TabsPhotos.defaultProps = {}

export default connect(({Travel}) => {
    let {data, loading, message} = Travel
    return {data, loading, message}
}, {})(React.memo(TabsPhotos))
