import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import Utils from '../../../utils/index'

const TravelCard = (props)=> {
    let {index,coordinates} = props
    return (
        <div className={`${[props?.className,'min-w-[220px]'].join(" ") ?? "col-span-1" } w-full h-[320px] rounded-xl shadow-xl relative overflow-hidden`}>


            <div className="h-[320px] w-full relative overflow-hidden z-[1]">
                <img src={typeof(props?.photo) !== "undefined" && Array.isArray(props?.photo) && props?.photo?.length > 0 ? [props?.photo[0]?.prefix,props?.photo[0]?.url].join(""):"https://images.unsplash.com/photo-1563911302283-d2bc129e7570?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=435&q=80 435w"}
                     alt="images"
                     className={'w-full h-full transition duration-200 !hover:scale-110 absolute top-0 left-0 Z-[0]  object-cover'}
                />
            </div>
            {
                typeof(props?.locations?.coordinates) !== "undefined" && Array.isArray(props?.locations?.coordinates) && props?.locations?.coordinates?.length > 0 &&
                props?.locations?.coordinates[0] !== null && props?.locations?.coordinates[1] !== null && (
                    <div className="yid-badge-location w-10 h-10 bg-cyan-500 p-1 z-[1]">
                        <a target={'_blank'} href={`https://www.google.com/maps?q=${props?.locations?.coordinates[1]},${props?.locations?.coordinates[0]}`}>
                            <div className="w-8 h-8 rounded-full bg-white relative">
                                                                    <span className="flex h-3 w-3 absolute top-0 right-0">
                                                                      <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-sky-400 opacity-75"/>
                                                                      <span className="relative inline-flex rounded-full h-3 w-3 bg-sky-500"/>
                                                                    </span>
                            </div>
                        </a>
                        <p>{[Utils.getLocation({from:coordinates?.from, to:coordinates?.to}),'km'].join(" ")}</p>
                    </div>
                )
            }
            <div className="w-full absolute z-[2] bottom-0 left-0 h-[120px] yid-gradient__primary p-2">
                <div className="w-full space-y-2">
                    <Link to={`/travel/p/${props?.slug ?? props?.travel_name}`}>
                        <h3 className={'text-white text-2xl font-semibold line-clamp-1'}>{props?.travel_name ?? props?.name ?? "-"}</h3>
                    </Link>
                    <p className={'text-white line-clamp-1'}>{props?.bio ?? props?.description}</p>
                </div>
            </div>

        </div>
    )
}

TravelCard.propTypes = {
    coordinates: PropTypes.shape({
        from: PropTypes.shape({
            lat:PropTypes.number,
            lng: PropTypes.number
        }),
        to: PropTypes.shape({
            lat: PropTypes.number,
            lng: PropTypes.number
        })
    })
}
TravelCard.defaultProps = {
    coordinates: {
        from:{
            lat:null,
            lng:null
        },
        to:{
            lat:null,
            lng:null
        }
    }
}


export default TravelCard