import React, {useRef, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Alert, Avatar, Button, Card, Input, Typography} from 'antd'
import moment from "moment";
import {CommentOutlined, LikeOutlined} from "@ant-design/icons";
import Flex from "../../../../components/shared-components/Flex";
import {motion} from "framer-motion";

const ReplyComment = (props) => {
    let {type, options, visible} = props
    const [value,setValue] = useState('')
    const onChange = (e)=> { setValue(e.target.value)}

    const onFinish = ()=> {
        if(typeof(props?.onAdd) !== "undefined"){
            props?.onAdd(value)
        }
    }
    return visible && (
        <div className="mb-3 d-flex">
            <Avatar src="/img/avatars/thumb-1.jpg"/>
            <div className="ml-2 bg-gray-lightest p-3 rounded w-100">
                <Input
                    onChange={onChange}
                    placeholder="Write comment"
                    suffix={
                        <div
                            onClick={onFinish}
                            className="cursor-pointer font-weight-semibold text-primary">
                            Send
                        </div>
                    }
                />
            </div>
        </div>
    )
}

ReplyComment.propTypes = {
    visible: PropTypes.bool.isRequired,
    type: PropTypes.string,
    options: PropTypes.shape({
        parentId: PropTypes.string,
        travelId: PropTypes.string
    }),
    onAdd: PropTypes.func.isRequired
}
ReplyComment.defaultProps = {
    visible: false,
    onAdd: (value)=> {
        console.log(value)
    }
}


const DiscussChildren = (props) => {
    let {elm} = props

    const elemInputRef = useRef(null)

    const [dataChild,setDataChild] = useState(()=> {
        if(typeof(elm?.reply) !== "undefined"){
            return elm?.reply
        }
        return []
    })
    const [visible, setVisible] = useState(false)
    const [showChild,setShowChild] = useState(true)
    const onReply = () => {
        let newVisible = !visible
        setVisible(newVisible)
        if(newVisible === true){
            if(elemInputRef && elemInputRef?.current){
                console.log(elemInputRef.current.offsetTop)
                console.log(window.innerHeight)
                window.scrollTo({
                    top:elemInputRef?.current?.offsetTop + (window.innerHeight / 2),
                    // block: 'start',
                    behavior:'smooth'
                })
            }
        }


    }

    const onAdd = (value) => {
        console.log()
        setDataChild([
            ...dataChild,
            {
                ...elm,
                comment: value,
            }
        ])
    }


    return (

        <div className="d-flex" key={elm?.id}>
            <div className="mt-2">
                <Avatar src={elm?.src}/>
            </div>
            <div className="ml-2 w-100">
                <div className={'bg-gray-lightest p-3 rounded w-100 mb-1'}>
                    <div className="d-flex align-items-center">
                        <h4 className="p-0 m-0">{[elm?.user?.firstName, elm?.user?.lastName].join(" ") ?? "-"}</h4>
                        <span className="mx-1"> | </span>
                        <span
                            className="font-size-sm text-dark">{moment(elm?.createdAt).format('DD MMMM YYYY')}</span>

                    </div>
                    <p className="mb-0 m-0 p-0">{elm?.comment}</p>
                    <div className="text-left">
                        <Button type="link" size={'small'} onClick={onReply} icon={<LikeOutlined/>} className={'p-0 m-0'}>Likes</Button>
                    {/*    <Button type="link" size={'small'} onClick={onReply} icon={<CommentOutlined/>}*/}
                    {/*            className={'p-0 ml-2'}>Reply</Button>*/}
                    </div>
                </div>

                {
                    Array.isArray(dataChild) && dataChild.length > 0 && (
                        <div className="text-right">
                            <Button type={'link'} className={'m-0 p-0'} onClick={()=> setShowChild(!showChild)}>{ showChild ? "hide all comment" : "show comment"}</Button>
                        </div>
                    )
                }

                {

                    showChild &&
                    Array.isArray(dataChild) &&
                    dataChild.map((item)=> {
                        return (
                            <DiscussChildren elm={item}/>
                        )
                    })
                }

                <div ref={elemInputRef}>
                    <ReplyComment
                        visible={visible}
                        onAdd={onAdd}
                        type={'children'} options={{
                        travelId: elm?.id ?? null,
                        parentId: elm?._id ?? null
                    }}/>
                </div>
            </div>

        </div>

    )
}
DiscussChildren.propTypes = {
    elm: PropTypes.shape({
        user: PropTypes.shape({}),

    })
}
DiscussChildren.defaultProps = {
    elm: {}
}


const DiscussItem = (props) => {
    let {elm, travelId, data} = props

    const elemInputRef = useRef(null)
    const elemRepliesRef = useRef(null)
    const [dataChild, setDataChild] = useState(() => {
        if (typeof (elm?.replies) !== "undefined") {
            return elm?.replies
        }
        return []
    })
    const [dataLikes, setDataLikes] = useState(() => {
        if (typeof (elm?.likes) !== "undefined") {
            return elm?.likes
        }
        return []
    })


    const [visible, setVisible] = useState(true)
    const [showChildDiscuss,setShowChildDiscuss] = useState(false)
    const onReply = () => {

        if(!visible  === true && elemInputRef && elemInputRef?.current){
            console.log(elemInputRef.current.offsetTop)
            console.log(window.innerHeight)
            window.scrollTo({
                top:(elemInputRef?.current?.offsetTop + (window?.innerHeight - elemInputRef?.current?.offsetTop)) ?? 0,
                // block: 'start',
                behavior:'smooth'
            })
        }
        setVisible(!visible)

    }

    const onAdd = (value) => {
        console.log(value,'DISCUSS ITEM')
    }

    const getHeightReplies = (_ = false)=> {
        if(_){
            if(elemRepliesRef && elemRepliesRef.current){
                window.scrollTo({
                    top:(elemRepliesRef?.current?.offsetTop + elemRepliesRef.current.offsetHeight) + (window.innerHeight / 2),
                    behavior:"smooth"
                })
                return elemRepliesRef.current.offsetHeight
            }
        }else{
            return 0
        }

    }



    return (
        <div className="mb-3 d-flex" key={elm?.id}>
            <div className="mt-2">
                <Avatar src={elm?.src}/>
            </div>
            <div className="ml-2 w-100">
                <div className={`bg-gray-lightest p-3 rounded w-100 ${elm?.count_replies > 1 ? "mb-2":"mb-4"}`}>
                    <div className="d-flex align-items-center mb-2">
                        <h4 className="mb-0">{[elm?.user?.firstName, elm?.user?.lastName].join(" ") ?? "-"}</h4>
                        <span className="mx-1"> | </span>
                        <span
                            className="font-size-sm text-dark">{moment(elm?.createdAt).format('DD MMMM YYYY')}</span>
                    </div>
                    <p className="mb-0">{elm?.comment}</p>

                    <div className="text-left">
                        <Button type="link" icon={<LikeOutlined/>} className={'p-0'}>Likes</Button>
                        <Button type="link" onClick={onReply} icon={<CommentOutlined/>}
                                className={'p-0 ml-2'}>Reply ({elm?.count_replies - 1 ?? 0})</Button>
                    </div>
                </div>

                {
                    typeof(elm?.count_replies) !== "undefined" && elm?.count_replies > 1 &&(
                        <div className="w-100">
                            <Button
                                className={'p-0 m-0'}
                                type={'link'}
                                onClick={()=> setShowChildDiscuss(!showChildDiscuss)}
                            >{!showChildDiscuss ? "Show Replies":"Hidden replies"}</Button>
                        </div>
                    )
                }

                <motion.div
                    initial={{ height: 0 }}
                    animate={{
                        height: getHeightReplies(showChildDiscuss),
                        overflow:"hidden",
                    }}>
                    <div className={''} ref={elemRepliesRef}>
                        {
                            typeof (dataChild) !== "undefined" &&
                            Array.isArray(dataChild) &&
                            dataChild.length > 0 &&
                            dataChild.map((child, idx) => (
                                <DiscussChildren elm={child}/>
                            ))
                        }
                    </div>
                </motion.div>



                <div ref={elemInputRef}>
                    <ReplyComment
                        visible={visible}
                        onAdd={onAdd}
                        type={'master'} options={{
                        travelId: travelId ?? null
                    }}/>
                </div>

            </div>

        </div>
    )
}
const Discuss = (props) => {
    let {travelId} = props
    const [data, setData] = useState(() => {
        if (typeof (props?.data) !== "undefined") {
            return props?.data
        }
        return []
    })
    return data.length > 0 && data.map((elm, index) => (
        <DiscussItem
            {...props}
            travelId={travelId}
            data={data}
            elm={elm}
            key={`discuss-item-${index}`}
        />
    ))
}

Discuss.propTypes = {
    data: PropTypes.array.isRequired
}
Discuss.defaultProps = {
    data: []
}

const CompDiscussions = (props) => {
    let {data, error, loading} = props
    return (
        <Card title={
            <Flex alignItems={'center'}>
                <CommentOutlined/>
                <Typography.Title level={4}
                                  className={'m-0 p-0 ml-2'}>{`Discussions (${data?.discuss?.length ?? 0})`}</Typography.Title>
            </Flex>
        } loading={props?.loading ?? false}>
            <div className="d-flex">
                <div className="w-100">

                    <Discuss data={data?.discuss ?? []} travelId={data?._id ?? null}/>

                    <div className="mb-3 d-flex">
                        <Avatar src="/img/avatars/thumb-1.jpg"/>
                        <div className="ml-2 bg-gray-lightest p-3 rounded w-100">
                            <Input
                                placeholder="Write comment"
                                suffix={
                                    <div
                                        className="cursor-pointer font-weight-semibold text-primary">
                                        Send
                                    </div>
                                }
                            />
                        </div>
                    </div>
                </div>
            </div>
        </Card>
    )
}

CompDiscussions.propTypes = {}
CompDiscussions.defaultProps = {}

export default connect(({Travel}) => {
    let {data, error, loading} = Travel
    return {data, error, loading}
}, {})(React.memo(CompDiscussions))
