import React, {useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Card, Row, Col, Typography, Badge} from "antd"
import Cards from "../../../shared-components/cards";
import {_getTravelNearby} from "../../../../redux/actions";
import CardCoordinates from "../../../shared-components/cards/card-coordinates";
import {Link} from "react-router-dom";
import Utils from "../../../../utils";
import TravelCard from "../../../shared-components/cards/travel-card";
import {MyCarousel} from "../../../shared-components/carousel";

const TravelNearby = (props) => {
    let {_getTravelNearby, lat, long, nearby} = props

    let data = [
        {
            images: "https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
        },
        {
            images: "https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
        },
        {
            images: "https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
        },
        {
            images: "https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
        },
    ]

    useEffect(() => {
        _getTravelNearby({
            lat, long, max: 1000000,

        }, {
            page: 1,
            limit: 10,
            notIn: props?.notIn ?? []
        })
    }, [_getTravelNearby])

    return (
        <div className={props?.className ?? ""} style={{}}>
            <div className="flex items-center gap-6 overflow-x-auto pb-4">
                {
                    typeof (nearby?.data) !== "undefined" && Array.isArray(nearby?.data) &&
                    nearby.data.length > 0 &&
                    nearby?.data.map((item, index) => {
                        let images = ''
                        if (typeof (item?.photo) !== "undefined" && Array.isArray(item?.photo) && item?.photo.length > 0) {
                            images = [item?.photo[0]?.prefix, item?.photo[0]?.url].join("")
                        }
                        return (
                            <div className={'w-full min-w-[220px]  max-w-[220px] h-[320px]'}
                                 key={`index-card-nearby-${index}`}>
                                <div className="w-full h-full bg-white rounded-xl overflow-hidden relative">
                                    <CardCoordinates
                                        coordinates={{
                                            from: {
                                                lat: lat,
                                                lng: long
                                            },
                                            to: {
                                                lat: item?.locations?.coordinates[1],
                                                lng: item?.locations?.coordinates[0],
                                            }
                                        }}
                                    />
                                    <img
                                        alt={images}
                                        className={'w-full h-full object-cover transition duration-200 hover:scale-110 transform z-[1]'}
                                        src={images}
                                    />
                                    <div
                                        className="absolute bottom-0 left-0 py-4 w-full bg-gradient-to-t from-black p-2 space-y-3">
                                        <div>
                                            {
                                                typeof (item?.categories) !== "undefined" && Array.isArray(item?.categories) && item?.categories?.length > 0 && (
                                                    <div className="w-full overflow-hidden flex items-center gap-3">
                                                        {
                                                            item?.categories?.slice(0, 2)?.map((cItem) => {
                                                                return (
                                                                    <Badge
                                                                        className={'whitespace-nowrap '}
                                                                        color={cItem?.background ?? "#fff"}
                                                                        text={cItem?.name ?? "-"}
                                                                        key={cItem?._id}
                                                                        style={{color: "#fff"}}
                                                                    />
                                                                )
                                                            })
                                                        }
                                                        {
                                                            item?.categories?.slice(2)?.length > 0 && (
                                                                <span
                                                                    className={'text-white'}>{["+", item?.categories?.slice(2)?.length ?? 0].join("")}</span>
                                                            )
                                                        }
            
                                                    </div>
                                                )
                                            }
                                        </div>
                                        <div className="">
                                            <Link to={`/travel/p/${item?.slug}`}>
                                                <h3 className={'text-white p-0 m-0 leading-6'}>{item?.name ?? item?.roomName ?? item?.room_name ?? "-"}</h3>
                                            </Link>
                                            <span className={'text-white p-0 m-0 text-xs'}>{item?.slug}</span>
                                        </div>
                                        <div className="flex justify-between">
                                            {
                                                !item?.is_free ? (
                                                    <p className={'text-white'}>{[Utils.numberFormat(item?.price?.regular) ?? 0, 'day'].join("/")}</p>
                                                ) : (
                                                    <span className={'text-white'}>FREE</span>
            
                                                )
                                            }
            
                                            {!item?.is_free && (
                                                <p className={'text-white'}>{item?.currency ?? "-"}</p>
                                            )}
            
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    )
}

TravelNearby.propTypes = {
    _getTravelNearby: PropTypes.func.isRequired,
    title: PropTypes.string,
    className: PropTypes.string,
    style: PropTypes.shape({}),
    notIn: PropTypes.array
}
TravelNearby.defaultProps = {
    title: "Travel Nearby",
    className: "",
    style: {},
    notIn: []
}

export default connect(({Geo, Travel}) => {
    let {current} = Geo
    let {collections} = Travel
    let {nearby} = collections
    return {...current, nearby}
}, {_getTravelNearby})(React.memo(TravelNearby))
