import React from 'react'
import PropTypes from 'prop-types'
import Utils from "../../../../utils";
import {EnvironmentOutlined} from '@ant-design/icons'
import {Link} from "react-router-dom";


const CardCoordinates = (props) => {
    let {coordinates} = props
    console.log({coordinates})
    return (
        <div>

            <div className="yid-loc absolute top-2 right-2 rounded-full w-10 h-10 bg-cyan-500 p-1">
                <div className={'yid-loc-icon'}>
                    <a target={'_blank'}
                       href={`https://www.google.com/maps?q=${coordinates?.to?.lat},${coordinates?.to?.lng}`}>
                        <EnvironmentOutlined/>
                    </a>
                </div>
                <div className="yid-loc-content">
                    <a target={'_blank'}
                       href={`https://www.google.com/maps?q=${coordinates?.to?.lat},${coordinates?.to?.lng}`}>
                        <p className={'p-0 m-0 whitespace-nowrap'}>{[Utils.getLocation({
                            from: coordinates?.from,
                            to: coordinates?.to
                        }), 'km'].join(" ")}</p>
                    </a>
                </div>


            </div>
        </div>
    )
}

CardCoordinates.propTypes = {
    coordinates: PropTypes.shape({
        from: PropTypes.shape({
            lat: PropTypes.any.isRequired,
            lng: PropTypes.any.isRequired
        }).isRequired,
        to: PropTypes.shape({
            lat: PropTypes.any.isRequired,
            lng: PropTypes.any.isRequired
        }).isRequired
    }).isRequired
}
CardCoordinates.defaultProps = {
    coordinates: {
        from: {
            lat: null,
            lng: null
        },
        to: {
            lat: null,
            lng: null
        },
    }
}


export default CardCoordinates
